import React, { useCallback, useEffect, useState, useRef } from 'react';
import {ip} from '../../store/ip'
import axios from 'axios';
import ChatContent from "./ChatContent";
import { storage } from '../../firebase/firebase'

import { Button, Grid, IconButton, Input, InputBase } from '@material-ui/core';
import ImageOutlinedIcon from '@material-ui/icons/ImageOutlined';
import SendOutlinedIcon from '@material-ui/icons/SendOutlined';

function ChatRoom(props) {
    const [room, setRoom] = props.useRoom
    const user_id =  window.sessionStorage.getItem('id')

    const [text, setText] = useState("")
    const [chatlog, setChatlog] = useState([])

    useEffect(() => {
        getChatlog()
    }, room)
    const hiddenFileInput = React.useRef(null);
    
    const isSeller = (id) => {
        return room.seller_id === id
    }

    const getChatlog = () => {
        if(!room.id)
            return
        axios.get(ip + "/chat/chatlog", {params: {id: room.id}})
        .then(res => {
            setChatlog(res.data.chatlog[0])
        })
        .catch(err => {
            console.log(err)
            alert(err)
        })
    }
    // type 0:   1:  2:  3:  4:  5:  10: 
    const sendMessage = (type, url="") => {
        if(!type && !text)
            return
        const log = {chat_id: room.id, user_id: user_id, contents: type ? url : text, type: type}
        axios.post(ip + "/chat/chatlog", log)
        .then(res => {
            if(type)
                setRoom(res.data.room[0])
            getChatlog()
            setText("")
        })
        .catch(err => {
            console.log(err)
            alert(err)
        })
    }
    const reqest = () => {
        sendMessage(1)
    }
    const acept = () => {
        sendMessage(2)
    }
    const purchase = () => {
        sendMessage(3)
    }
    const cancel = () => {
        sendMessage(10)
    }
    const confirm = () => {
        sendMessage(4)
    }
    //    
    const CancelBt = <Button size={"small"} type={"button"} onClick={() => cancel()}>  </Button>     
    const ActionButton = () => {
        switch(room.state){
            case 0: 
                if(!isSeller(user_id))
                    return <Button size={"small"} type={"button"} onClick={() => reqest()}>  </Button>
                break
            case 1:
                if(isSeller(user_id))
                    return <Button size={"small"} type={"button"} onClick={() => acept()}>  </Button>
                break
            case 2:
                if(!isSeller(user_id))
                    return <div>
                        {CancelBt}
                        <Button size={"small"} type={"button"} onClick={() => purchase()}> () </Button>
                    </div>
                return CancelBt
            case 4:
                if(!isSeller(user_id))
                    return <div>
                        {CancelBt}
                        <Button size={"small"} type={"button"} onClick={() => confirm()}>  </Button>
                    </div>
                return CancelBt
        }
        return <div></div>
    }

    //  
    const handleClick = e => {
        hiddenFileInput.current.click();
    };
    const uploadImage = (e) => {
        if(e.target.files[0]){
        const img = e.target.files[0]
        const uploadTask = storage.ref(`images/${img.name}`).put(img);
        uploadTask.on('state_changed',
            (snapshot) => {
                console.log(snapshot)
            }, (err) => {
                console.log(err)
            }, () => {
                storage.ref('images').child(img.name).getDownloadURL().then(url => {
                    sendMessage(20, url)
                })
            });
        }
    }
    return(
        <div>
            <div style={{overflow: "scroll", height: "350px"}}>
                {
                    chatlog.map((message, idx) => {
                        const align = (message.user_id === user_id) ? "right" : "left"
                        return(
                            <li key={idx} style={{textAlign: align, margin: '1rem'}}>
                                <ChatContent message={message} sendMessage={sendMessage}/>
                            </li>
                        )
                    })
                }
            </div>
            <Grid container direction={'column'} style={{borderTop: '1px solid gray'}}>
                <Grid item>
                    {/* Enter    Ctrl+Enter  */}
                    <InputBase onChange={(e) => setText(e.target.value)}
                        value={text}
                        style={{paddingLeft: '1rem'}}
                        inputProps={{ 'aria-label': 'naked', size: 'small'}}
                        type="text"
                        fullWidth multiline rows={3}
                        autoFocus/>
                    </Grid>
                <Grid item style={{display: 'flex', direction: 'rtl'}}>
                    <IconButton size="small" onClick={() => sendMessage(0)}>
                        <SendOutlinedIcon />
                    </IconButton>
                    <div>
                        <IconButton size="small" onClick={handleClick}>
                            <ImageOutlinedIcon/>
                            <input accept="image/*" type="file"
                                onChange={uploadImage}
                                ref={hiddenFileInput}
                                style={{display: 'none'}}
                            />
                        </IconButton>
                    </div>
                    <ActionButton/>
                </Grid>
            </Grid>
        </div>
    )
}

export default ChatRoom;