import React, { useEffect, useState } from 'react'
import ChatRoom from './ChatRoom'
import axios from 'axios';
import {ip} from '../../store/ip'

import { Grid } from '@material-ui/core';
import { ToggleButton, ToggleButtonGroup } from '@material-ui/lab';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';

function Chat() {
    //   
    const user_id = window.sessionStorage.getItem("id")
    const [room, setRoom] = useState({})
    const [roomList, setRoomList] = useState([])
    const [view, setView] = React.useState('all');

    useEffect(() => {
        getRooms()
    }, room)

    // type 'all':   'shopping':   'selling': 
    const getRooms = (type=view) => {
        axios.get(ip + "/chat", {params: {id: user_id, type: type}})
        .then(res => {
            setRoomList(res.data.chats[0])
        })
        .catch(err => {
            console.log(err)
            alert(err)
        })
    }
    // 
    const renderLists = roomList.map((item, idx) => {
        return (
            <Grid container spacing = {3} key={idx} onClick={() => {setRoom(room === item ? {} : item)}}>
                <Grid item>
                    <h5> board_id : {item.board_id} </h5>
                </Grid>
                <Grid item>
                    <h2> {item.seller_id}  {item.buyer_id} </h2>
                </Grid>
            </Grid>
        )
    })

    const toggleChange = (event, nextView) => {
        if(nextView === null)
            return
        getRooms(nextView)
        setView(nextView)
    }
    const ROOM_STATE = ['', ' ', ' ', ' ', '']
    return (
        <Grid container spacing = {3}>
            <Grid item style={{textAlign: 'center'}}>
                <ToggleButtonGroup orientation="vertical" value={view} exclusive onChange={toggleChange}>
                    <ToggleButton value="all">
                        <h4></h4>
                    </ToggleButton>
                    <ToggleButton value="shopping">
                        <ShoppingCartIcon/>
                    </ToggleButton>
                    <ToggleButton value="selling">
                        <AttachMoneyIcon/>
                    </ToggleButton>
                </ToggleButtonGroup>
            </Grid>
            {/*   */}
            <Grid item>
                <div style={{padding: "1rem", border: "1px solid gray", background: '#FFF', height: '500px'}}>
                    {roomList.length ? renderLists : <div>   </div> }
                </div>
            </Grid>
            {/* 11  */}
            <Grid item style={{width: '300px'}}>
                { !("id" in room) ? null :
                <div>
                    <header style={{marginBottom: '1rem'}}>
                        <div>
                            <h2> {room.seller_id} </h2>
                            <h3> {room.state + " " + ROOM_STATE[room.state] + " "} </h3>
                        </div>
                    </header>
                    <div style={{border: "1px solid gray", background: '#FFF'}}>   
                        <ChatRoom useRoom={[room, setRoom]}/>
                    </div>
                </div>
                }
            </Grid>
        </Grid>
    )
}

export default Chat
