import React, { useEffect, useState } from "react"
import { withRouter } from "react-router-dom"
import {ip} from "../../store/ip"
import axios from "axios"

import { withStyles } from "@material-ui/core/styles";
import { Dialog, DialogTitle, Grid, TextField,Button } from "@material-ui/core";

const CenterDialog = withStyles((theme) => ({
    root: {
        textAlign : 'center',
        alignItems : 'center',
        fontSize: '10px',
        border:'1px',
        margin:'auto',
    }
}))(Dialog);

const ColorButton = withStyles((theme)=>({
    root: {
        background: '#585A6A',
        color: 'white',
        margin:'2px',
        height:'30px',
        width:'80px',
    }
}))(Button);

function Dialogtest() {
    const [Open, setOpen] = useState(false)
    const [frCheck, setFrCheck] = useState(0)

    const id = window.sessionStorage.getItem('id')
    const [pw, setPw] = useState("")
    const [nickname, setNickname] = useState("")
    const [name, setName] = useState("")
    const [phone, setPhone] = useState("")
    const [address, setAddress] = useState("")
    const [birth, setBirth] = useState("")
    const [school, setSchool] = useState("")

    
    useEffect(() => {
        getUserInfo()
      }, [])

    const getUserInfo = () => {
        axios.get(ip + "/mypages/profile", {
            params: {
              id: id
            },
          })
          .then((data) => {
            const userData = data.data[0];    //

            setPw(userData.pw)
            setNickname(userData.nickname)
            setName(userData.name)
            setPhone(userData.phone)
            setAddress(userData.address)
            setBirth(userData.birth)
            setSchool(userData.school)
          });
    }

    const updateUserInfo = () => {
        axios.post(ip + "/mypages/updateinfo", {
                id : id,
                nickname : nickname,
                address : address,
            })
            .then((res) => {
                console.log(res);
                alert(' !')
            })
            .catch (err => {
                console.log(err)
                alert('  .')
        })
        closeAndRefresh()
    }

    const checkDeleteAgain = () => {
        return window.confirm("  ?")
    }


    const setForeignkey = (frNum) => {
        var result = false
        setFrCheck(frNum)
        console.log("state : "+frCheck)
        axios
            .post(ip+'/mypages/set',{
                frCheck : frCheck
            })
            .then((res) => {
                console.log(res)
                result = true;
            })
            .catch (err => {
                console.log(err)
                alert(frCheck+' .')
                result = false;
        })
        return result
    }


    const deleteUserInfo = () => {
        if (checkDeleteAgain()) {
            console.log("  : "+id)
            axios
                .post(ip + "/mypages/deleteinfo", {
                    id : id
                })
                .then((res) => {
                    console.log(res);
                    alert(' .')
                    //!window.location.replace("?")
                })
                .catch (err => {
                    console.log(err)
                    alert('.')
            })
        }
        

        closeAndRefresh()
    }

    const closeAndRefresh = () => {
        setOpen(false)
        window.location.reload()
    }
    
    return (
        <div>
            <Button variant="text" onClick={() => setOpen(true)}>  </Button>

            <CenterDialog open={Open}>
                <DialogTitle>  </DialogTitle>
                <form>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <TextField label="ID" value={id} disabled readOnly />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField label="Nickname" value={nickname} onInput={e => setNickname(e.target.value)}/>
                        </Grid>
                        <Grid item xs={12}>
                            <TextField label="Name" value={name} disabled readOnly />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField label="Phone Number" value={phone} disabled readOnly />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField label="Address" value={address} onInput={e => setAddress(e.target.value)} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField label="Birth" value={birth} disabled readOnly />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField label="School" value={school} disabled readOnly />
                        </Grid>
                    </Grid>
                    <br></br>
                    <ColorButton variant="text" onClick={() => updateUserInfo()}></ColorButton>
                    <ColorButton variant="text" onClick={() => deleteUserInfo()}></ColorButton>
                    <ColorButton variant="text" onClick={() => closeAndRefresh()}></ColorButton>
                </form>
            </CenterDialog>
        </div>
    )
}

export default withRouter(Dialogtest)