import React from "react";
import { Link, withRouter } from "react-router-dom";
import { useSnackbar } from 'notistack';
import Store from "../store/store";

import logo_Image from "../lib/image/logo_1.png";

function Header(props) {

  const { enqueueSnackbar } = useSnackbar()
  const logout = (store) => {
    enqueueSnackbar(' ')
    store.onLogout()
    window.location.reload()
  }
  const nickname = window.sessionStorage.getItem("nickname")
  const id = window.sessionStorage.getItem("id")

  const loggedNav = (store) => {
    return <div>
      <Link to="/" onClick={() => logout(store)}> {id + " " + nickname + " / logout"} </Link>
      <Link to="/mypage" style={{marginLeft: "1rem"}}>   </Link>
    </div>
  }
  return (
    <div style={{maxWidth: '700px', height: '100px', margin: '2rem auto', textAlign: 'center'}}>
      <div style={{marginBottom: '0.5rem'}}>
        <img onClick={() =>  {props.history.push('/'); window.location.reload()}} style={{maxWidth: '600px', height: '100px', cursor: 'pointer'}} src={logo_Image} alt="" />
      </div>
      <Store.Consumer>
        {store => id ? loggedNav(store): <Link to="/login"> login / sign up </Link> }           
      </Store.Consumer>
    </div>
  )
}
export default withRouter(Header)
