import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import axios from 'axios';
import { useSnackbar } from 'notistack';
import {ip} from "../../store/ip"
import getImageUrls from "../../firebase/getImageUrls";
import Alert from "../home/Alert";
import ImageUpload from "./ImageUpload";
import Progress from "./Progress";

import { Button, IconButton, InputAdornment, InputBase, makeStyles, Typography } from '@material-ui/core'
import { Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core'
import DeleteOutlineRoundedIcon from '@material-ui/icons/DeleteOutlineRounded';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import DoneIcon from '@material-ui/icons/Done';
import CloseIcon from '@material-ui/icons/Close';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';

const useStyles = makeStyles({
    title: {
        fontWeight: 'bold',
        fontSize: 'large',
        height: '13px'
    },
    imageWrap : {
        width: "100%",
        height: "200px",
        float: "right",
        overflow_y:"scroll",
      },
    imageStyle :{
        resize: "both",
        float: "center",
        maxWidth: "200px",
        height: "auto",
    },
    imageBoxStyle : {
        width: "200px",
        height: "200px",
        display: "inline-block",
        border: "1px solid #e4e4e4",
        overflow: "hidden",
        marginTop:"20px",
    },
});

//       
function BoardDetail(props) {
    const classes = useStyles()
    const { enqueueSnackbar } = useSnackbar()
    const user_id = window.sessionStorage.getItem("id")
    const [open, setOpen] = props.useOpen
    //var board = JSON.parse(JSON.stringify(props.board)) //  
    const [board, getBoards] = props.useBoard
    const oriImgAry = board.images === '' ? [] : board.images.split(" ")
    
    const [title, setTitle] = useState('')
    const [price, setPrice] = useState('')
    const [contents, setContents] = useState('')
    const [imgUrls, setImgUrls] = useState('')
    const [imgAry,setImgAry] = useState([])
    const [imgBase64Ary, setImgBase64Ary] = useState([]) // 
    useEffect(() => {
        setTitle(board.title)
        setPrice(board.price)
        setContents(board.contents)
        setImgUrls('')
        setImgAry(board.images.split(" "))
        setImgBase64Ary([])
    }, board)

    const [edit, setEdit] = useState(false)
    const [wish, setWish] = props.useWish   
    const [alertOption, setAlertOption] = useState({})
    const loginAlert = {open: true, text: "..", doAfter: () => {props.history.push("/login")}}

    const [openDialog, setOpenDialog] = useState(false) 
    const [loading, setLoading] = useState(0)

    const deleteBoard = () => {
        axios.post(ip+'/board/delete', {id: board.id})
            .then(() => {
                setOpen(false)
                getBoards()
                enqueueSnackbar('  ', { variant: 'success'})
            })
            .catch(err => {
                console.log(err)
                alert("  ")
            })
    }
    const preEditBoard = (vaild) => {
        setEdit(false)
        if(vaild === false){
            setTitle(board.title); setPrice(board.price); setContents(board.contents)
            setImgUrls(''); setImgAry(oriImgAry); setImgBase64Ary([]);
            return
        }

        var isImgChange = false
        var isNewImage = false
        if(oriImgAry.length === imgAry.length){
            oriImgAry.map((img, idx) => {
                if(img !== imgAry[idx])
                    isNewImage = true
            })
        } else {
            isImgChange = true
            imgAry.map((img) => {
                if(typeof(img) !== 'string')
                    isNewImage = true
            })
        }
        if(isNewImage){
            getImageUrls({imgAry: imgAry, setLoading: setLoading, setImgUrls: setImgUrls})
            setOpenDialog(true)
        }
        else{
            var tmpUrls = ""
            imgAry.map((url) => { tmpUrls = (tmpUrls === "" ? url : tmpUrls + " " + url) })
            editBoard(isImgChange, tmpUrls)
        }
        
  }
    const editBoard = (imgChange, url) => {
        if(board.title === title && !imgChange && board.contents === contents && board.price === price)
            return
        const body  = {
            board_id: board.id,
            title: title,
            images: url,
            contents: contents,
            price: price
        }
        axios.post(ip+'/board/edit', body)
            .then(() => {
                getBoards()
                enqueueSnackbar(' ', { variant: 'success'})
                setOpenDialog(false)
                setOpen(false)
            })
            .catch(err => {
                console.log(err)
                alert("  ")
            })
    }

    const creatChat = () => {
        if(!user_id)
            return setAlertOption(loginAlert)

        axios.post(ip+'/chat', {seller_id: board.seller_id, buyer_id: user_id, board_id: board.id})
            .then(() => {
                setOpen(false)
                enqueueSnackbar(board.seller_id + '  .')
            })
            .catch(err => {
                console.log(err)
                alert("  ")
            })
    }
    const wishBoard = () => {
        if(!user_id)
            return setAlertOption(loginAlert)
            
        axios.post(ip+'/board/wish', {isWish: wish, user_id: user_id, board_id: board.id})
        .then(() => {
            const message = wish ? "  [] !" : "  [] !"
            setWish(!wish)
            enqueueSnackbar(board.title + message)
        })
        .catch(err => {
            console.log(err)
            alert("  ")
        })
    }

    //  , 3 ,     XX (sql price  9 )
    const priceChange = (e) => {
        const value = e.target.value.replace(/[^0-9]/g, '')
        if(value.length < 10)
            setPrice(value)
    }
    
    const BoardAction = () => {
        if(board.seller_id === user_id)
            return (<div style={{display: 'flex'}}>
                { edit ? <div> 
                    <IconButton onClick={() => preEditBoard(true)}> 
                        <DoneIcon/>
                    </IconButton>
                    <IconButton onClick={() => preEditBoard(false)}>
                        <CloseIcon/>
                    </IconButton>
                </div> :
                    <IconButton onClick={() => setEdit(true)}>
                        <EditOutlinedIcon/>
                    </IconButton>
                }
                    <IconButton onClick={() => deleteBoard()}>
                        <DeleteOutlineRoundedIcon/>
                    </IconButton>
            </div>)
        else return (
                <div>
                    <IconButton onClick={()=>wishBoard()}>
                        {wish ? <FavoriteIcon/> : <FavoriteBorderIcon/>}
                    </IconButton>
                    <Button autoFocus onClick={()=>creatChat()} color="primary">
                         
                    </Button>
                </div>
    )}
    /*    */
    return (
        <Dialog open={open} onClose={() => {preEditBoard(false); setOpen(false)}} scroll = 'paper' maxWidth={"lg"}>
            {/*   */}
            <DialogTitle>
                <InputBase className={classes.title}
                    onChange={(e) => setTitle(e.target.value)}
                    value={title}
                    fullWidth
                    readOnly={!edit}  
                />
                <Typography>
                    {board.seller_id}
                </Typography>

                <div>
                    <InputBase
                        onChange={(e) => priceChange(e)}
                        value={String(price).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                        readOnly={!edit}
                        startAdornment={<InputAdornment position="start"></InputAdornment>}
                    />
                    <Typography variant="caption"> (ETH) <span style={{fontWeight: 'bold'}}>{price / 522500}</span> </Typography>
                </div>
            </DialogTitle>

            {/*   */}
            <DialogContent dividers style={{ minWidth: 500, maxWidth: 650, minHeight: 600 }}>
                <DialogContentText tabIndex={-1}>
                    <InputBase
                        onChange={(e) => setContents(e.target.value)}
                        value={contents}
                        multiline
                        rows={7} rowsMin={7}
                        readOnly={!edit}
                    />
                </DialogContentText>

                { edit ? <ImageUpload useImgAry={[imgAry, setImgAry]} useImgBase64Ary={[imgBase64Ary, setImgBase64Ary]} /> 
                    : <div>{
                        imgAry.map((image,index) => {
                            if(typeof(image) === 'string' && image !== '')
                            return (
                                <div key={index} className={classes.imageBoxStyle} >
                                    <img className={classes.imageStyle} alt={"img"} src={image} height="300" width="400"/>
                                </div>
                        )})
                    }</div>
                }
                <Dialog open={openDialog}>
                    <DialogContent style={{width: "400px", textAlign: "center"}}>
                        <Progress value={loading}/>
                        <DialogContentText>
                              
                        </DialogContentText>
                        <IconButton onClick={() => editBoard(true, imgUrls)} disabled={loading !== 100}>
                            <DoneIcon/>
                        </IconButton>
                        <IconButton onClick={() => setOpenDialog(false)}>
                            <CloseIcon/>
                        </IconButton>
                    </DialogContent>
                </Dialog>
            </DialogContent>

            {/*  */}
            <DialogActions>
                <BoardAction/>
            </DialogActions>
            <Alert option={alertOption}/>
        </Dialog>
    )
}
export default withRouter(BoardDetail)