import React, { useEffect, useRef } from 'react';

function ChatContent(props) {
    const message = props.message
    const messagesEndRef = useRef(null)

    const scrollToBottom = () => {
        messagesEndRef.current.scrollIntoView({ behavior: "smooth" })
    }
    useEffect(scrollToBottom, message)

    const contents = (message) => {
        switch(message.type){
            //  
            default: case 0:
                return(<h3>{message.contents}</h3>)
            //  
            case 1:
                return <div>
                    <h4> {message.user_id}  . </h4>
                    <p>  ''   . </p>
                </div>
            //  
            case 2:
                return <div>
                    <h4> {message.user_id}   </h4>
                    <p>  '()' ! </p>
                </div>
            case 3:
                return <div>
                    <h4> {message.user_id}   </h4>
                    <p>    '' ! </p>
                </div>
            case 4:
                return <div>
                    <h4> {message.user_id}   </h4>
                    <p>  . </p>
                </div>
            case 10:
                return <div>
                    <h4> {message.user_id}  .</h4>
                </div>
            case 20:
                return (<img style={{maxWidth: '150px', maxHeight: '150px'}} alt='img' 
                src={message.contents} onClick={() => { window.open(message.contents) }} />)
        }
    }
    return (
        <div>
            <div>
                <h4>{message.user_id}</h4>
                {message.sendTime.replace(/T/, ' ').replace(/\..+/, '')}
            </div>  
            <div style={{padding: "3px", marginBottom: "3px"}}>
                {contents(message)}
            </div>
            <div  ref={messagesEndRef}/>
        </div> 
    )
}

export default ChatContent
