import React, { Component, useEffect, useState } from "react"
import { withRouter } from "react-router-dom"
import { withStyles } from "@material-ui/core/styles";
import profile from "../../lib/image/profile.jpg"
import { ip } from "../../store/ip"
import axios from "axios"
import Dialogtest from "./dialogtest"

import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
import StoreOutlinedIcon from '@material-ui/icons/StoreOutlined';
import FavoriteOutlinedIcon from '@material-ui/icons/FavoriteOutlined';
import { Button, Table, TableHead, TableBody, TableRow, TableCell, Box } from '@material-ui/core'

const ColorButton = withStyles((theme) => ({
    root: {
        background: '#585A6A',
        border: 0,
        borderRadius: 3,
        color: 'white',
        height: 48,
        padding: '0 30px',
        margin: theme.spacing(3, 1, 2),
        width: '300px',
        fontSize: '20px',
    },
}))(Button);

const CenterBox = withStyles((theme) => ({
    root: {
        textAlign: 'center',
        alignItems: 'center',
        border:'2px',
        margin:'auto',
    },
}))(Box);

const ProfileBox = withStyles((theme) => ({
    root: {
        textAlign: 'center',
    },
}))(Box);

const CenterTable = withStyles((theme) => ({
    root: {
        textAlign:'center',
    }
}))(Table);

function MypageProfile() {

    const [id, setId] = useState(window.sessionStorage.getItem('id'))
    const [nickname, setNickname] = useState("")
    const [level, setLevel] = useState("")
    const [responseRate, setResponseRate] = useState("")

    //mysell 
    const [allMysellList, setAllMysellList] = useState([])
    const [mysellvisibility, setMysellvisibility] = useState("hidden")
    const [myselldisplay, setMyselldisplay] = useState("none")

    //mybuy
    const [allMybuyList, setAllMybuyList] = useState([])
    const [mybuyvisibility, setMybuyvisibility] = useState("hidden")
    const [mybuydisplay, setMybuydisplay] = useState("none")

    //mywish 
    const [allMywishList, setAllMywishList] = useState([])
    const [mywishvisibility, setMywishvisibility] = useState("hidden")
    const [mywishdisplay, setMywishdisplay] = useState("none")

    useEffect(() => {
        getUserInfo()
        getMysell()
        getMybuy()
        getMywish()
    }, [])

    const getUserInfo = () => {
        axios
            .get(ip + "/mypages/profile", {
                params: {
                    id: id //  
                },
            })
            .then((data) => {
                const userData = data.data[0];    //
                console.log(userData)

                setNickname(userData.nickname)
                setLevel(userData.level)
                setResponseRate(userData.responseRate)
            });
    }

    const getMysell = () => {
        axios
            .get(ip + "/mypages/mysell", {
                params: {
                    user_id: id //   
                },
            })
            .then((data) => {

                const list = data.data;    //
                console.log(list)

                setAllMysellList(list)

                console.log("user id : " + id);
            })
            .catch(err => console.log(err));
    }

    const getMybuy = () => {
        axios
            .get(ip + "/mypages/mybuy", {
                params: {
                    user_id: id // 
                },
            })
            .then((data) => {
                const list = data.data;    //
                console.log(list)

                setAllMybuyList(list)
            })
            .catch(err => console.log(err));
    }

    const getMywish = () => {
        axios
            .get(ip + "/mypages/mywish", {
                params: {
                    user_id: id // 
                },
            })
            .then((data) => {
                const list = data.data;    //
                console.log(list)
                setAllMywishList(list)
            })
            .catch(err => console.log(err));
    }

    //0:  1: 2: 
    const boardState = (conState) => {
        if (conState === 0) {
            return ""
        }
        else if (conState === 1) {
            return ""
        }
        else if (conState === 2) {
            return ""
        }
        else return ""
    }

    const viewDetail = (cid) => {
        console.log("  " + cid)
    }

    const displayMysell = () => {
        setMysellvisibility("visible"); setMyselldisplay("block")
        setMybuyvisibility("hidden"); setMybuydisplay("none")
        setMywishvisibility("hidden"); setMywishdisplay("none")
    }

    const displayMybuy = () => {
        setMysellvisibility("hidden"); setMyselldisplay("none")
        setMybuyvisibility("visible"); setMybuydisplay("block")
        setMywishvisibility("hidden"); setMywishdisplay("none")
    }

    const displayMywish = () => {
        setMysellvisibility("hidden"); setMyselldisplay("none")
        setMybuyvisibility("hidden"); setMybuydisplay("none")
        setMywishvisibility("visible"); setMywishdisplay("block")
    }

    return (
        <div class="mypage">
            <CenterBox component="div">
                <ProfileBox component="div">
                    <img src={profile} width="100" height="100" />
                    <h2>{nickname} !</h2>
                    <br></br>
                    <h3>  {responseRate} % </h3>
                    <h3>  Lv.{level} </h3>
                    <br></br>
                    <br></br>
                    <Dialogtest/>
                </ProfileBox>

                <Box component="div" display="block">
                    <ColorButton onClick={() => displayMysell()}><StoreOutlinedIcon color="white"/>&nbsp;&nbsp; </ColorButton>
                    <ColorButton onClick={() => displayMybuy()}><ShoppingCartOutlinedIcon color="white"/>&nbsp;&nbsp;  </ColorButton>
                    <ColorButton onClick={() => displayMywish()}><FavoriteOutlinedIcon color="white"/>&nbsp;&nbsp; </ColorButton>
                </Box>
                <Box component="div" minHeight="500px">
                    {/* mysell */}
                    <Box component="div" display={myselldisplay} visibility={mysellvisibility} width="60%" margin="auto">
                        <CenterTable>
                            <TableHead>
                                <TableRow>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {allMysellList ? allMysellList.map((c, index) => (
                                    <TableRow row={c} key={index}>
                                        <TableCell width="20%">{c.id}&nbsp;&nbsp;&nbsp;&nbsp;<Button variant="primary" onClick={() => viewDetail(c.id)}>Detail</Button></TableCell>
                                        <TableCell width="20%">{c.title}</TableCell>
                                        <TableCell width="20%"><img src={c.images} width="50px" height="50px" /></TableCell>
                                        <TableCell width="20%">{c.price}</TableCell>
                                        <TableCell width="20%">{boardState(c.state)}</TableCell>
                                    </TableRow>

                                )) : ""}
                            </TableBody>
                        </CenterTable>
                    </Box>

                    {/* mybuy */}
                    <Box component="div" display={mybuydisplay} visibility={mybuyvisibility} width="60%" margin="auto">
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {allMybuyList ? allMybuyList.map((c, index) => (

                                    <TableRow row={c} key={index}>
                                        <TableCell width="20%">{c.id}&nbsp;&nbsp;&nbsp;&nbsp;<Button variant="primary" onClick={() => viewDetail(c.id)}>Detail</Button></TableCell>
                                        <TableCell width="20%">{c.title}</TableCell>
                                        <TableCell width="20%"><img src={c.images} width="50px" height="50px" /></TableCell>
                                        <TableCell width="20%">{c.board_price}</TableCell>
                                        <TableCell width="20%">{c.contractDate}</TableCell>
                                    </TableRow>

                                )) : ""}
                            </TableBody>
                        </Table>
                    </Box>

                    {/* mywish */}
                    <Box component="div" display={mywishdisplay} visibility={mywishvisibility} width="60%" margin="auto">
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                    <TableCell width="20%"></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {allMywishList ? allMywishList.map((c, index) => (

                                    <TableRow row={c} key={index}>
                                        <TableCell width="20%">{c.board_id}&nbsp;&nbsp;&nbsp;&nbsp;<Button variant="primary" onClick={() => viewDetail(c.board_id)}>Detail</Button></TableCell>
                                        <TableCell width="20%">{c.title}</TableCell>
                                        <TableCell width="20%"><img src={c.images} width="50px" height="50px" /></TableCell>
                                        <TableCell width="20%">{c.price}</TableCell>
                                        <TableCell width="20%">{boardState(c.state)}</TableCell>
                                    </TableRow>

                                )) : ""}
                            </TableBody>
                        </Table>
                    </Box>
                </Box>
            </CenterBox>
        </div>

    )
}

export default withRouter(MypageProfile)