import React, { useState, withStyles } from 'react';
import ContactInfo from "./emailinfo"
import EmailDetails from "./emailDetails"
import "../../lib/css/email.css";
import "../../lib/css/common.css";
import axios from 'axios';
import {ip} from "../../store/ip"

import SchoolIcon from '@material-ui/icons/School';
import {Dialog, Divider, DialogContent, DialogActions, IconButton, Typography} from '@material-ui/core';
import Alert from "@material-ui/lab/Alert";

const useStyles = () => ({
  dialogPaper: {
    minHeight: '90%',
    maxHeight: '90%',
    minwidth: '90%',
    maxwidth: '90%',
},
  submit: {
  },
  color:{
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
  }
})

function SchoolCheck(props) {

    const classes = useStyles();

    const [open, setOpen] = useState(false)
    const [keyword, setKeyword] = useState('')
    const [selectedKey, setSelectedKey] = useState(-1)
    const [school, setSchool] = useState([])
    const [searchClick, setSearchClick] = useState(false)
    const [searchnum, setSearchnum] = useState(-1)
    const [emailkey, setEmailkey] = useState('')
    const [codekey, setCodekey] = useState(0)
    const [iscodekey, setIscodekey] = useState(0)
    const [emailcheck, setEmailcheck] = useState(false)
    const [codecheck, setCodecheck] = useState(false)
    const [isMatchCode, setIsMatchCode] = useState(false)
    const [totalcheck, setTotalcheck] = useState(false)

    const getcsv = async() =>{
      var csvFilePath = require("./school2.csv");
      var Papa = require("papaparse/papaparse.min");
      Papa.parse(csvFilePath, {
        header:true,
        download: true,
        skipEmptyLines: true,
        complete: this.updateData
      });
    }

    const updateData = (res) => {
      setSchool(res.data)
    }

    /**   ..*/

    //   
    const randcode = () => { 
      codekey = Math.random().toString().substr(2,6);
      getcode();
      setEmailcheck(true)
    }

    //    
    const getcode = () => { 
        axios.get(ip + '/email', { params: {
            emailkey: emailkey,
            codekey: codekey, // 
        }})
    }

    //   
    const handelemail = (e) => {
        setEmailkey(e.target.value)
    }

    //   
    const SendTest = (e) => {
        if(selectedKey !== -1)
            props.onSubmit(school[selectedKey], totalcheck);
        else
            props.onSubmit(0,0);
    }
    //  
    const handlecode = (e) => {  
        setIscodekey(e.target.value)
        checkMatchCode()
    }
    const checkMatchCode = () => {
      let timer;
      if (timer)
        clearTimeout(timer)

      timer = setTimeout(() => {
          setIsMatchCode(codekey === iscodekey)
      }, 500);
    }
    const checkcodeClick = () => {
        setCodecheck(true)
    }
//-----------------------------------------------------------------------------------------------------------//

    //  
    const handleClickOpen = () => { 
        setOpen(true)
    }
    const handleClose = () => {
        setOpen(false)
        setEmailcheck(false)
        setCodecheck(false)
        setKeyword('')
        setTotalcheck(false)
    }
      
    const handeltotalCheck = () => {
        if(codekey!==0 && iscodekey!==0)
        {
          if(codekey === iscodekey){
              setOpen(false)
              setEmailcheck(false)
              setCodecheck(false)
              setTotalcheck(false)
              setKeyword('')
          }
        }else{
          this.setState({
            open: false,
            emailcheck:false,
            codecheck:false,
            totalcheck:false,
            keyword:'', // 
          })
        }
    }

    //

    const handleChange = (e) => { //  
        setKeyword(e.target.value)
    }
    const handleClick=(key)=>{ ///
        setSelectedKey(key)
    }
    const handleSearch = () => {
        setSearchClick(true)
        setSearchnum(-searchnum)
    }
    /**-------------------------------------------------------------- */
  
    const mapToComponents = (data) => {
        data.sort((a,b) => { return a.name > b.name; })
        data = data.filter( (schoolinfo) => { 
            return schoolinfo.school.toLowerCase().indexOf(this.state.keyword.toLowerCase()) > -1;
        } )
        if (keyword !== '' && searchnum === 1) {
          return data.map((contact, i) => {
            return (
                <ContactInfo
                    contact={contact} 
                    key={i}
                    isSelected={selectedKey !== -1}
                    onClick={()=>handleClick(contact.number)}
                    //    
                    />
               );
            });
        } 
        if(searchnum===1)
          handleSearch();
    }

    return (
    <div>
        <IconButton onClick={() => setOpen(true)}>
            <SchoolIcon fontSize="large"/>
        </IconButton>
        <Typography variant="overline">
            *   
        </Typography>
        <Dialog onClose={handleClose} open={open} classes={{ paper: classes.dialogPaper }}>
            <Divider variant="middle" style={{ marginBottom: 25 }} />
            <DialogContent >
                <div class="school_Wrap" >
                    <h1>[ ]</h1>
                    <div class="school">
                        <form name="header_search_box"
                            action="#"
                            method="get"
                            target="_blank">
                            <input type="search"
                                name="keyword"
                                placeholder=" "
                                onChange={handleChange}/>
                        </form>
                        <button className="header_button" type="button" onClick={() => handleSearch()}></button>
                    </div>
                            
                    <div class="school_result">              
                        {mapToComponents(school)}
                        <br/>
                        <br/>
                        <EmailDetails 
                            isSelected={selectedKey != -1}
                            email={school[selectedKey]}/>
                    </div> 
                </div>

                <Divider variant="middle" style={{ marginBottom: 25 }}  style={{height:3}}/>
                {/*  */}
                <div class="email_Wrap" >
                    <h1>[ ]</h1>
                    <div class="email">
                        <form name="header_search_box"
                            action="#"
                            method="get"
                            target="_blank">

                            <input type="search"
                                name="keyword"
                                placeholder=" (abc123@...)"
                                onChange={handelemail}/>
                            {/*       */}
                        </form>
                        <button className="emial_header_button" onClick={randcode}>
                            EMAL  
                        </button>
                        { emailcheck ? <Alert variant="filled" severity="success"> ! </Alert> : null }
                    </div>
                                                    
                    {/*  */}

                    <div>
                        <h1 class="check">[]</h1>
                        <div class="email">
                        {/*    */}
                            <form name="header_search_box"
                                action="#"
                                method="get"
                                target="_blank">
                                <input type="search"
                                    name="keyword"
                                    placeholder=" "
                                    onChange={handlecode}/>
                            </form>
                            <button className="emial_header_button" type="button" onClick={checkcodeClick}> </button>
                                {codecheck ? (codekey ? (
                                    isMatchCode ? <Alert variant="filled" severity="success">  </Alert>
                                    : <Alert variant="filled" severity="error">  </Alert>):null):null}
                        </div>
                    </div> 
                </div>
            </DialogContent>
            <DialogActions style={{height:'60px',margin:'0 auto'}} >
                <div class="button_Wrap">
                    <form onSubmit={SendTest} >
                        <button className="check_button"
                            type="submit"
                            fullWidth
                            variant="contained"
                            onClick={handeltotalCheck}>
                            
                        </button>
                        <button className="check_button"
                                type="submit"
                                fullWidth
                                variant="contained"
                                onClick={handleClose}> </button>
                                    
                    </form>
                                
                </div>
            </DialogActions>
        </Dialog>
    </div>
    )
}
export default SchoolCheck;

